mall-font-size: .875rem;\n    --smaller-font-size: .813rem;\n\n    /*===== Font Weight =====*/\n    --font-medium: 500;\n    --font-bold: 600;\n\n    /*===== Margenes Bottom =====*/\n    --mb-0-25: .25rem;\n    --mb-0-5: .5rem;\n    --mb-0-75: .75rem;\n    --mb-1: 1rem;\n    --mb-1-5: 1.5rem;\n    --mb-2: 2rem;\n    --mb-2-5: 2.5rem;\n    --mb-3: 3rem;\n\n    /*===== Z Index =====*/\n    --z-fixed: 10;\n    --z-modal: 100;\n}\n\n/* Responsive Typography */\n@media screen and (max-width: 1024px) {\n    :root {\n        --biggest-font-size: 2rem;\n        --h1-font-size: 1.5rem;\n        --h2-font-size: 1.25rem;\n        --h3-font-size: 1.125rem;\n        --normal-font-size: .938rem;\n        --small-font-size: .813rem;\n        --smaller-font-size: .75rem;\n    }\n}\n\n/*=============== BASE ===============*/\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nhtml {\n    scroll-behavior:smooth;\n\n}\n\nbody, button, input {\n    font-family: var(--body-font);\n    font-size: var(--normal-font-size);\n}\n\nbody {\n    background-color: var(--body-color);\n    color: var(--text-color);\n}\n\nh1, h2, h3 {\n    color: var(--title-color);\n    font-weight: var(--font-bold);\n}\n\nul {\n    list-style: none;\n}\n\na {\n    text-decoration: none;\n}\n\nimg {\n    max-width: 100%;\n    height: auto;\n}\n\nbutton {\n    cursor: pointer;\n}\n\nbutton, input {\n    border: none;\n    outline: none;\n}\n\n/*=============== LAYOUT ===============*/\n.container {\n    max-width: 1250px;\n    margin-left: auto;\n    margin-right: auto;\n}\n\n.grid {\n    display: grid;   \n}\n\n\n/*=============== REUSABLE CSS CLASSES ===============*/\n.section {\n    padding: 6.5rem 0 2rem;\n}\n\n.section__title {\n    text-align: center;\n    font-size: var(--h1-font-size);\n    margin-bottom: var(--mb-3);\n}\n\n.section__title::before {\n    content: attr(data-heading);\n    display: block;\n    font-size: var(--normal-font-size);\n    font-weight: var(--font-medium);\n    color: var(--skin-color);\n}\n\n\n\n\n// /*=============== MAIN ===============*/\n// .main {\n//     margin-left: 100px;\n// }\n\n\n\n/*===== Buttons =====*/\n.button {\n    display: inline-flex;\n    align-items: center;\n    column-gap: .5rem;\n    background-color: var(--skin-color);\n    color: var(--title-color);\n    padding: .75rem 1.4rem;\n    border-radius: .25rem;\n    font-weight: var(--font-medium);\n    position: relative;\n    z-index: 1;\n}\n\n.button::after {\n    position: absolute;\n    content: '';\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    background-color: var(--hover-color);\n    z-index: -1;\n    transform-origin: center;\n    transform: scale(0);\n    border-radius: .25rem;\n    transition: .3s;\n}\n\n.button:hover::after {\n    transform: scale(1);\n}\n\n.button__icon {\n    font-size: 1.25rem;\n}\n\n\n\n\n/*===== Skills Section =====*/\n.skills__container {\n    grid-template-columns: 360px 320px;\n    column-gap: 3rem;\n    justify-content: center;\n}\n\n.skills__header {\n    display: flex;\n    align-items: center;\n    cursor: pointer;\n}\n\n.skills__header:not(:last-child) {\n    margin-bottom: var(--mb-2-5);\n}\n\n.skills__icon, .skills__arrow {\n    font-size: 2rem;\n    color: var(--skin-color);\n}\n\n.skills__icon {\n    margin-right: var(--mb-0-75);\n}\n\n.skills__title {\n    font-size: var(--h3-font-size);\n    font-weight: var(--font-medium);\n}\n\n.skills__subtitle {\n    font-size: var(--small-font-size);\n}\n\n.skills__arrow {\n    margin-left: auto;\n}\n\n.skills__active .skills__arrow {\n    transform: rotate(-90deg);\n    transition: .3s;\n}\n\n.skills [data-content] {\n    display: none;\n}\n\n.skills__active[data-content] {\n    display: block;\n}\n\n.skills__list {\n    row-gap: 1.8rem;\n}\n\n.skills__titles {\n    display: flex;\n    justify-content: space-between;\n    margin-bottom: var(--mb-0-5);\n}\n\n.skills__name {\n    font-size: var(--normal-font-size);\n    font-weight: var(--font-medium);\n}\n\n.skills__bar, .skills__percentage {\n    height: 5px;\n    border-radius: .25rem;\n}\n\n.skills__bar {\n    background-color: var(--box-color);\n}\n\n.skills__percentage {\n    display: block;\n    background-color: var(--skin-color);\n}\n\n\n\n/*===== Services Section =====*/\n.services__container {\n    grid-template-columns: repeat(3, 250px);\n    justify-content: center;\n    column-gap: 1.8rem;\n}\n\n.services__content {\n    position: relative;\n    background-color: var(--box-color);\n    padding: 6rem 0 2rem 2.5rem;\n    border-radius: .25rem;\n}\n\n.services__icon {\n    display: block;\n    font-size: 1.8rem;\n    color: var(--skin-color);\n    margin-bottom: var(--mb-1);\n}\n\n.services__title {\n    font-size: var(--h3-font-size);\n    margin-bottom: var(--mb-1);\n    font-weight: var(--font-medium);\n}\n\n.services__button {\n    color: var(--skin-color);\n    font-size: var(--small-font-size);\n    display: flex;\n    align-items: center;\n    column-gap: .25rem;\n    cursor: pointer;\n}\n\n.services__button-icon {\n    font-size: 1rem;\n    transition: .3s;\n}\n\n.services__button:hover .services__button-icon {\n    transform: translateX(.25rem);\n}\n\n.services__modal {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: rgba(0, 0, 0, 0.5);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 0 1rem;\n    z-index: var(--z-modal);\n    transition: .3s;\n    opacity: 0;\n    visibility: hidden;\n}\n\n.services__modal-content {\n    width: 500px;\n    position: relative;\n    background-color: var(--box-color);\n    padding: 4.5rem 2.5rem 2.5rem;\n    border-radius: .5rem;\n}\n\n.services__modal-close {\n    position: absolute;\n    top: 1.5rem;\n    right: 1.5rem;\n    font-size: 1.5rem;\n    color: var(--skin-color);\n    cursor: pointer;\n}\n\n.services__modal-title, .services__modal-description {\n    text-align: center;\n}\n\n.services__modal-title {\n    font-size: var(--h3-font-size);\n    font-weight: var(--font-medium);\n    margin-bottom: var(--mb-1);\n}\n\n.services__modal-description {\n    font-size: var(--small-font-size);\n    padding: 0 3.5rem;\n    margin-bottom: var(--mb-2);\n}\n\n.services__modal-services {\n    row-gap: var(--mb-0-75);\n}\n\n.services__modal-service {\n    display: flex;\n    align-items: center;\n    column-gap: .5rem;\n}\n\n.services__modal-icon {\n    color: var(--skin-color);\n    font-size: 1.1rem;\n}\n\n.services__modal-info {\n    font-size: var(--small-font-size);\n}\n\n/* Active Modal*/\n.active-modal {\n    opacity: 1;\n    visibility: visible;\n}\n\n\n\n/*=============== SCROLL UP ===============*/\n\n\n/* Show Scroll Up*/\n\n\n/*=============== SCROLL BAR ===============*/\n::-webkit-scrollbar {\n    width: .60rem;\n    // background-color: var(--body-color);\n    background-color: rgb(206, 198, 198);\n    background-color: black;\n    // border-radius: .2rem;\n}\n\n::-webkit-scrollbar-thumb {\n    background-color: var(--skin-color);\n    border-radius: .2rem;\n    height: 60px;\n}\n\n\n/*========== BREAKPOINTS ==========*/\n/* For Large Devices */\n@media screen and (max-width: 1408px) {\n    .container {\n        margin-left: var(--mb-2-5);\n        margin-right: var(--mb-2-5);\n    }\n}\n\n@media screen and (max-width: 1216px) {\n    .about__container {\n        column-gap: 2.5rem;\n    }\n\n    .about__description {\n        padding-right: 0;\n    }\n\n    .work__container {\n        grid-template-columns: repeat(2, 330px);\n        gap: 2.5rem;\n    }\n}\n\n@media screen and (max-width: 1024px) {\n    .container {\n        margin-left: var(--mb-1-5);\n        margin-right: var(--mb-1-5);\n    }\n\n    .sidebar {\n        width: 100%;\n        z-index: 999;\n        transform: translateX(-100%);\n    }\n\n    .show-sidebar {\n        transform: translateX(0);\n    }\n\n    .nav__logo,\n    .btn__share {\n        display: none;\n    }\n\n    .nav__menu {\n        height: 100%;\n        width: 100%;\n        transform: rotate(0deg) translateX(0);\n        display: flex;\n        justify-content: center;\n    }\n\n    .nav__list {\n        flex-direction: column;\n        height: 100%;\n        justify-content: center;\n        align-items: center;\n    }\n\n    .nav__link {\n        padding: 1rem 0;\n        line-height: 1.5rem;\n    }\n\n    .nav__link.active-link::after, .nav__link:hover::after {\n        bottom: 0;\n    }\n\n    .nav__close {\n        display: block;\n    }\n\n    .nav__toggle {\n        display: flex;\n    }\n\n    .main {\n        margin-left: 0;\n    }\n\n    .about__container {\n        grid-template-columns: 1fr;\n        row-gap: 2.5rem;\n    }\n\n    .about__img {\n        width: 350px;\n    }\n\n    .about__data {\n        text-align: center;\n\n    }\n\n    .about__info {\n        justify-content: center;   \n    }\n\n    .about__box {\n        padding: .75rem .5rem;\n    }\n\n    .about__description {\n        padding: 0 4rem;\n        text-align: center;\n    }\n\n    .qualification__container {\n        grid-template-columns: repeat(2, 290px);\n    }\n\n    .skills__container {\n        grid-template-columns: 340px 300px;\n    }\n\n    .skills__list {\n        row-gap: 1.7rem;\n    }\n\n    .work__card {\n        padding: 1rem;\n    }\n\n    .work__img {\n        margin-bottom: var(--mb-0-75);\n    }\n\n    .work__title {\n        margin-bottom: var(--mb-0-25);\n    }\n\n    .services__container {\n        grid-template-columns: repeat(3, 220px);\n    }\n\n    .testimonial__card {\n        padding: 1.25rem 1.5rem;\n    }\n\n    .portfolio__item-details {\n        margin-bottom: var(--mb-1-5);\n    }\n\n    .details__info li {\n        margin-bottom: var(--mb-0-5);\n    }\n\n    .details__title {\n        margin-bottom: var(--mb-0-75);\n    }\n}\n\n/* For Medium Devices */\n@media screen and (max-width: 768px) {\n    .about__img {\n        width: 250px;\n    }\n\n    .qualification__container {\n        grid-template-columns: 290px;\n        row-gap: 3rem;\n    }\n\n    .skills__container {\n        grid-template-columns: 300px;\n        row-gap: 3rem;\n    }\n\n    .work__container {\n        grid-template-columns: 330px;\n    }\n\n    .services__container {\n        grid-template-columns: repeat(2, 1fr);\n        gap: 1.5rem;\n    }\n\n    .services__content {\n        padding: 3.5rem .5rem 1.25rem 1.5rem;\n    }\n\n    .services__icon {\n        font-size: 1.5rem;\n    }\n\n    .contact__container {\n        grid-template-columns: 360px;\n        row-gap: 3rem;\n    }\n\n    .footer__container {\n        grid-template-columns: repeat(2, 1fr);\n        row-gap: 3rem;\n      \n    }\n\n    .footer__socials {\n        justify-self: start;\n    }\n\n    .footer__links {\n        flex-direction: column;\n        row-gap: 1.5rem;\n    }\n\n    .footer__bg {\n        padding: 2rem 0 3rem;\n    }\n\n    .footer__copy {\n        margin-top: var(--mb-3);\n    }\n\n    .portfolio__popup-inner {\n        width: 420px;\n        padding: 2.8rem 1.5rem 2.5rem;\n    }\n\n    .portfolio__popup-content {\n        grid-template-columns: 1fr;\n        row-gap: 1.6rem;\n    }\n\n    .details__title {\n        font-size: var(--normal-font-size);\n    }\n\n    .portfolio__popup-close {\n        top: .5rem;\n    }\n\n}\n\n@media screen and (max-width: 576px) {\n    .nav__toggle {\n        right: initial;\n        left: 1.5rem;\n    }\n\n    .home {\n        background-image: none;\n        height: initial;\n        align-items: initial;\n        padding: 7rem 0 2rem;\n\n    }\n\n    .home__container {\n        row-gap: 2rem;\n    }\n\n    .home__img {\n        display: block;\n        width: 250px;\n        justify-self: center;\n    }\n\n    .home__social {\n        left: initial;\n        right: -1rem;\n        flex-direction: column;\n        row-gap: 3.5rem;\n    }\n\n    .home__social-follow {\n        font-size: var(--smaller-font-size);\n        transform: rotate(90deg);\n    }\n\n    .home__social-links {\n        flex-direction: column;\n        row-gap: .25rem;\n    }\n\n    .home__social-link {\n        font-size: var(--normal-font-size);\n    }\n\n    .my__info {\n        display: none;\n    }\n\n    .about__info {\n        grid-template-columns: repeat(3, 1fr);\n    }\n\n    .about__description {\n        padding: 0;\n    }\n\n    .services__modal-content {\n        padding: 4.5rem 1.5rem 2.5rem;\n    }\n\n    .services__modal-description {\n        padding: 0;\n    }\n\n    .work__container,\n    .contact__container {\n        grid-template-columns: 300px;\n    }\n\n    .footer__container {\n        grid-template-columns: 1fr;\n    }\n\n    .footer__links {\n        justify-self: flex-start;\n    }  \n}\n\n/* For Small Devices */\n@media screen and (max-width: 350px) {\n    .container {\n        margin-left: var(--mb-1);\n        margin-right: var(--mb-1);\n    }\n\n    .home__img {\n        width: 200px;\n    }\n\n    .home__title {\n        font-size: var(--h1-font-size);\n    }\n\n    .home__subtitle {\n        font-size: var(--normal-font-size);\n    }\n\n    .about__info {\n        grid-template-columns: repeat(2, 1fr);\n        row-gap: .5rem;\n    }\n\n    .skills__container,\n    .qualification__container,\n    .work__container,\n    .contact__container {\n        grid-template-columns: 1fr;\n    }\n\n    .skills__title {\n        font-size: var(--normal-font-size);\n    }\n\n    .work__item {\n    font-size: var(--small-font-size);\n    }\n\n    .work__filters {\n        column-gap: .25rem;\n    }\n\n    .services__container {\n        grid-template-columns: max-content;\n    }\n\n    .services__content {\n        padding-right: 3.5rem;\n    }\n}","/*=============== GOOGLE FONTS ===============*/\n@import url(\"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap\");\n/*=============== VARIABLES CSS ===============*/\n:root {\n  /*===== Colors =====*/\n  --hue-color:242;\n  --hue-color: orange;\n  /* HSL Color Mode */\n  --skin-color: rgb(239, 168, 35);\n  --hover-color: rgb(0, 167, 204);\n  --title-color: hsl(var(--hue-color), 8%, 95%);\n  --text-color: hsl(var(--hue-color), 8%, 85%);\n  --body-color: hsl(var(--hue-color), 19%, 5%);\n  --body-color: #f9f8f6;\n  --box-color: hsl(var(--hue-color), 14%, 10%);\n  --scroll-bar-color: hsl(var(--hue-color), 12%, 38%);\n  --scroll-thumb-color: hsl(var(--hue-color), 12%, 26%);\n  /*===== Font and Typography =====*/\n  --body-font: \"Poppins\", sans-serif;\n  /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */\n  --biggest-font-size: 3rem;\n  --h1-font-size: 2.25rem;\n  --h2-font-size: 1.5rem;\n  --h3-font-size: 1.25rem;\n  --normal-font-size: 1rem;\n  --small-font-size: .875rem;\n  --smaller-font-size: .813rem;\n  /*===== Font Weight =====*/\n  --font-medium: 500;\n  --font-bold: 600;\n  /*===== Margenes Bottom =====*/\n  --mb-0-25: .25rem;\n  --mb-0-5: .5rem;\n  --mb-0-75: .75rem;\n  --mb-1: 1rem;\n  --mb-1-5: 1.5rem;\n  --mb-2: 2rem;\n  --mb-2-5: 2.5rem;\n  --mb-3: 3rem;\n  /*===== Z Index =====*/\n  --z-fixed: 10;\n  --z-modal: 100;\n}\n\n/* Responsive Typography */\n@media screen and (max-width: 1024px) {\n  :root {\n    --biggest-font-size: 2rem;\n    --h1-font-size: 1.5rem;\n    --h2-font-size: 1.25rem;\n    --h3-font-size: 1.125rem;\n    --normal-font-size: .938rem;\n    --small-font-size: .813rem;\n    --smaller-font-size: .75rem;\n  }\n}\n/*=============== BASE ===============*/\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody, button, input {\n  font-family: var(--body-font);\n  font-size: var(--normal-font-size);\n}\n\nbody {\n  background-color: var(--body-color);\n  color: var(--text-color);\n}\n\nh1, h2, h3 {\n  color: var(--title-color);\n  font-weight: var(--font-bold);\n}\n\nul {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n}\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n\nbutton {\n  cursor: pointer;\n}\n\nbutton, input {\n  border: none;\n  outline: none;\n}\n\n/*=============== LAYOUT ===============*/\n.container {\n  max-width: 1250px;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.grid {\n  display: grid;\n}\n\n/*=============== REUSABLE CSS CLASSES ===============*/\n.section {\n  padding: 6.5rem 0 2rem;\n}\n\n.section__title {\n  text-align: center;\n  font-size: var(--h1-font-size);\n  margin-bottom: var(--mb-3);\n}\n\n.section__title::before {\n  content: attr(data-heading);\n  display: block;\n  font-size: var(--normal-font-size);\n  font-weight: var(--font-medium);\n  color: var(--skin-color);\n}\n\n/*===== Buttons =====*/\n.button {\n  display: inline-flex;\n  align-items: center;\n  column-gap: 0.5rem;\n  background-color: var(--skin-color);\n  color: var(--title-color);\n  padding: 0.75rem 1.4rem;\n  border-radius: 0.25rem;\n  font-weight: var(--font-medium);\n  position: relative;\n  z-index: 1;\n}\n\n.button::after {\n  position: absolute;\n  content: \"\";\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  background-color: var(--hover-color);\n  z-index: -1;\n  transform-origin: center;\n  transform: scale(0);\n  border-radius: 0.25rem;\n  transition: 0.3s;\n}\n\n.button:hover::after {\n  transform: scale(1);\n}\n\n.button__icon {\n  font-size: 1.25rem;\n}\n\n/*===== Skills Section =====*/\n.skills__container {\n  grid-template-columns: 360px 320px;\n  column-gap: 3rem;\n  justify-content: center;\n}\n\n.skills__header {\n  display: flex;\n  align-items: center;\n  cursor: pointer;\n}\n\n.skills__header:not(:last-child) {\n  margin-bottom: var(--mb-2-5);\n}\n\n.skills__icon, .skills__arrow {\n  font-size: 2rem;\n  color: var(--skin-color);\n}\n\n.skills__icon {\n  margin-right: var(--mb-0-75);\n}\n\n.skills__title {\n  font-size: var(--h3-font-size);\n  font-weight: var(--font-medium);\n}\n\n.skills__subtitle {\n  font-size: var(--small-font-size);\n}\n\n.skills__arrow {\n  margin-left: auto;\n}\n\n.skills__active .skills__arrow {\n  transform: rotate(-90deg);\n  transition: 0.3s;\n}\n\n.skills [data-content] {\n  display: none;\n}\n\n.skills__active[data-content] {\n  display: block;\n}\n\n.skills__list {\n  row-gap: 1.8rem;\n}\n\n.skills__titles {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: var(--mb-0-5);\n}\n\n.skills__name {\n  font-size: var(--normal-font-size);\n  font-weight: var(--font-medium);\n}\n\n.skills__bar, .skills__percentage {\n  height: 5px;\n  border-radius: 0.25rem;\n}\n\n.skills__bar {\n  background-color: var(--box-color);\n}\n\n.skills__percentage {\n  display: block;\n  background-color: var(--skin-color);\n}\n\n/*===== Services Section =====*/\n.services__container {\n  grid-template-columns: repeat(3, 250px);\n  justify-content: center;\n  column-gap: 1.8rem;\n}\n\n.services__content {\n  position: relative;\n  background-color: var(--box-color);\n  padding: 6rem 0 2rem 2.5rem;\n  border-radius: 0.25rem;\n}\n\n.services__icon {\n  display: block;\n  font-size: 1.8rem;\n  color: var(--skin-color);\n  margin-bottom: var(--mb-1);\n}\n\n.services__title {\n  font-size: var(--h3-font-size);\n  margin-bottom: var(--mb-1);\n  font-weight: var(--font-medium);\n}\n\n.services__button {\n  color: var(--skin-color);\n  font-size: var(--small-font-size);\n  display: flex;\n  align-items: center;\n  column-gap: 0.25rem;\n  cursor: pointer;\n}\n\n.services__button-icon {\n  font-size: 1rem;\n  transition: 0.3s;\n}\n\n.services__button:hover .services__button-icon {\n  transform: translateX(0.25rem);\n}\n\n.services__modal {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: rgba(0, 0, 0, 0.5);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0 1rem;\n  z-index: var(--z-modal);\n  transition: 0.3s;\n  opacity: 0;\n  visibility: hidden;\n}\n\n.services__modal-content {\n  width: 500px;\n  position: relative;\n  background-color: var(--box-color);\n  padding: 4.5rem 2.5rem 2.5rem;\n  border-radius: 0.5rem;\n}\n\n.services__modal-close {\n  position: absolute;\n  top: 1.5rem;\n  right: 1.5rem;\n  font-size: 1.5rem;\n  color: var(--skin-color);\n  cursor: pointer;\n}\n\n.services__modal-title, .services__modal-description {\n  text-align: center;\n}\n\n.services__modal-title {\n  font-size: var(--h3-font-size);\n  font-weight: var(--font-medium);\n  margin-bottom: var(--mb-1);\n}\n\n.services__modal-description {\n  font-size: var(--small-font-size);\n  padding: 0 3.5rem;\n  margin-bottom: var(--mb-2);\n}\n\n.services__modal-services {\n  row-gap: var(--mb-0-75);\n}\n\n.services__modal-service {\n  display: flex;\n  align-items: center;\n  column-gap: 0.5rem;\n}\n\n.services__modal-icon {\n  color: var(--skin-color);\n  font-size: 1.1rem;\n}\n\n.services__modal-info {\n  font-size: var(--small-font-size);\n}\n\n/* Active Modal*/\n.active-modal {\n  opacity: 1;\n  visibility: visible;\n}\n\n/*=============== SCROLL UP ===============*/\n/* Show Scroll Up*/\n/*=============== SCROLL BAR ===============*/\n::-webkit-scrollbar {\n  width: 0.6rem;\n  background-color: #cec6c6;\n  background-color: black;\n}\n\n::-webkit-scrollbar-thumb {\n  background-color: var(--skin-color);\n  border-radius: 0.2rem;\n  height: 60px;\n}\n\n/*========== BREAKPOINTS ==========*/\n/* For Large Devices */\n@media screen and (max-width: 1408px) {\n  .container {\n    margin-left: var(--mb-2-5);\n    margin-right: var(--mb-2-5);\n  }\n}\n@media screen and (max-width: 1216px) {\n  .about__container {\n    column-gap: 2.5rem;\n  }\n\n  .about__description {\n    padding-right: 0;\n  }\n\n  .work__container {\n    grid-template-columns: repeat(2, 330px);\n    gap: 2.5rem;\n  }\n}\n@media screen and (max-width: 1024px) {\n  .container {\n    margin-left: var(--mb-1-5);\n    margin-right: var(--mb-1-5);\n  }\n\n  .sidebar {\n    width: 100%;\n    z-index: 999;\n    transform: translateX(-100%);\n  }\n\n  .show-sidebar {\n    transform: translateX(0);\n  }\n\n  .nav__logo,\n.btn__share {\n    display: none;\n  }\n\n  .nav__menu {\n    height: 100%;\n    width: 100%;\n    transform: rotate(0deg) translateX(0);\n    display: flex;\n    justify-content: center;\n  }\n\n  .nav__list {\n    flex-direction: column;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n  }\n\n  .nav__link {\n    padding: 1rem 0;\n    line-height: 1.5rem;\n  }\n\n  .nav__link.active-link::after, .nav__link:hover::after {\n    bottom: 0;\n  }\n\n  .nav__close {\n    display: block;\n  }\n\n  .nav__toggle {\n    display: flex;\n  }\n\n  .main {\n    margin-left: 0;\n  }\n\n  .about__container {\n    grid-template-columns: 1fr;\n    row-gap: 2.5rem;\n  }\n\n  .about__img {\n    width: 350px;\n  }\n\n  .about__data {\n    text-align: center;\n  }\n\n  .about__info {\n    justify-content: center;\n  }\n\n  .about__box {\n    padding: 0.75rem 0.5rem;\n  }\n\n  .about__description {\n    padding: 0 4rem;\n    text-align: center;\n  }\n\n  .qualification__container {\n    grid-template-columns: repeat(2, 290px);\n  }\n\n  .skills__container {\n    grid-template-columns: 340px 300px;\n  }\n\n  .skills__list {\n    row-gap: 1.7rem;\n  }\n\n  .work__card {\n    padding: 1rem;\n  }\n\n  .work__img {\n    margin-bottom: var(--mb-0-75);\n  }\n\n  .work__title {\n    margin-bottom: var(--mb-0-25);\n  }\n\n  .services__container {\n    grid-template-columns: repeat(3, 220px);\n  }\n\n  .testimonial__card {\n    padding: 1.25rem 1.5rem;\n  }\n\n  .portfolio__item-details {\n    margin-bottom: var(--mb-1-5);\n  }\n\n  .details__info li {\n    margin-bottom: var(--mb-0-5);\n  }\n\n  .details__title {\n    margin-bottom: var(--mb-0-75);\n  }\n}\n/* For Medium Devices */\n@media screen and (max-width: 768px) {\n  .about__img {\n    width: 250px;\n  }\n\n  .qualification__container {\n    grid-template-columns: 290px;\n    row-gap: 3rem;\n  }\n\n  .skills__container {\n    grid-template-columns: 300px;\n    row-gap: 3rem;\n  }\n\n  .work__container {\n    grid-template-columns: 330px;\n  }\n\n  .services__container {\n    grid-template-columns: repeat(2, 1fr);\n    gap: 1.5rem;\n  }\n\n  .services__content {\n    padding: 3.5rem 0.5rem 1.25rem 1.5rem;\n  }\n\n  .services__icon {\n    font-size: 1.5rem;\n  }\n\n  .contact__container {\n    grid-template-columns: 360px;\n    row-gap: 3rem;\n  }\n\n  .footer__container {\n    grid-template-columns: repeat(2, 1fr);\n    row-gap: 3rem;\n  }\n\n  .footer__socials {\n    justify-self: start;\n  }\n\n  .footer__links {\n    flex-direction: column;\n    row-gap: 1.5rem;\n  }\n\n  .footer__bg {\n    padding: 2rem 0 3rem;\n  }\n\n  .footer__copy {\n    margin-top: var(--mb-3);\n  }\n\n  .portfolio__popup-inner {\n    width: 420px;\n    padding: 2.8rem 1.5rem 2.5rem;\n  }\n\n  .portfolio__popup-content {\n    grid-template-columns: 1fr;\n    row-gap: 1.6rem;\n  }\n\n  .details__title {\n    font-size: var(--normal-font-size);\n  }\n\n  .portfolio__popup-close {\n    top: 0.5rem;\n  }\n}\n@media screen and (max-width: 576px) {\n  .nav__toggle {\n    right: initial;\n    left: 1.5rem;\n  }\n\n  .home {\n    background-image: none;\n    height: initial;\n    align-items: initial;\n    padding: 7rem 0 2rem;\n  }\n\n  .home__container {\n    row-gap: 2rem;\n  }\n\n  .home__img {\n    display: block;\n    width: 250px;\n    justify-self: center;\n  }\n\n  .home__social {\n    left: initial;\n    right: -1rem;\n    flex-direction: column;\n    row-gap: 3.5rem;\n  }\n\n  .home__social-follow {\n    font-size: var(--smaller-font-size);\n    transform: rotate(90deg);\n  }\n\n  .home__social-links {\n    flex-direction: column;\n    row-gap: 0.25rem;\n  }\n\n  .home__social-link {\n    font-size: var(--normal-font-size);\n  }\n\n  .my__info {\n    display: none;\n  }\n\n  .about__info {\n    grid-template-columns: repeat(3, 1fr);\n  }\n\n  .about__description {\n    padding: 0;\n  }\n\n  .services__modal-content {\n    padding: 4.5rem 1.5rem 2.5rem;\n  }\n\n  .services__modal-description {\n    padding: 0;\n  }\n\n  .work__container,\n.contact__container {\n    grid-template-columns: 300px;\n  }\n\n  .footer__container {\n    grid-template-columns: 1fr;\n  }\n\n  .footer__links {\n    justify-self: flex-start;\n  }\n}\n/* For Small Devices */\n@media screen and (max-width: 350px) {\n  .container {\n    margin-left: var(--mb-1);\n    margin-right: var(--mb-1);\n  }\n\n  .home__img {\n    width: 200px;\n  }\n\n  .home__title {\n    font-size: var(--h1-font-size);\n  }\n\n  .home__subtitle {\n    font-size: var(--normal-font-size);\n  }\n\n  .about__info {\n    grid-template-columns: repeat(2, 1fr);\n    row-gap: 0.5rem;\n  }\n\n  .skills__container,\n.qualification__container,\n.work__container,\n.contact__container {\n    grid-template-columns: 1fr;\n  }\n\n  .skills__title {\n    font-size: var(--normal-font-size);\n  }\n\n  .work__item {\n    font-size: var(--small-font-size);\n  }\n\n  .work__filters {\n    column-gap: 0.25rem;\n  }\n\n  .services__container {\n    grid-template-columns: max-content;\n  }\n\n  .services__content {\n    padding-right: 3.5rem;\n  }\n}\n/*=============== HEADER ===============*/\n.header {\n  position: fixed;\n  width: 100vw;\n  height: 60px;\n  background-color: rgba(255, 255, 255, 0.462);\n  border-bottom: 1px solid var(--skin-color);\n  transition: 0.5s;\n  z-index: 1000;\n}\n\n.nav__logo {\n  position: absolute;\n  left: 1.5rem;\n  top: 50%;\n  transform: translateY(-50%);\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  background-color: var(--skin-color);\n  text-align: center;\n  margin: auto;\n  border: 1px solid;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.nav__logo-text {\n  font-size: 1.2rem;\n  color: white;\n  font-weight: var(--font-bold);\n  line-height: 45px;\n}\n.nav__logo-text img {\n  height: 30px;\n  display: block;\n}\n\n.nav__menu {\n  position: fixed;\n  top: 15px;\n}\n\n.menu {\n  display: flex;\n}\n\n.nav__list {\n  display: flex;\n}\n\n.nav__link {\n  height: 100%;\n  padding: 0 1rem;\n  color: var(--title-color);\n  font-weight: var(--font-medium);\n  position: relative;\n  transition: 0.4s;\n}\n\n.btn__share {\n  position: absolute;\n  bottom: 1.8rem;\n  left: 0;\n  right: 0;\n  text-align: center;\n}\n\n.social__share {\n  font-size: 1.5rem;\n}\n\n.nav__toggle {\n  height: 32px;\n  width: 36px;\n  cursor: pointer;\n  position: fixed;\n  right: 1.5rem;\n  top: 2rem;\n  font-size: 1.2rem;\n  border-radius: 0.25rem;\n  background-color: var(--skin-color);\n  color: var(--title-color);\n  display: none;\n  justify-content: center;\n}\n\n.nav__close {\n  font-size: 1.5rem;\n  position: absolute;\n  top: 1rem;\n  right: 1.25rem;\n  cursor: pointer;\n  display: none;\n}\n\n/* Active Link */\n.nav__link.active-link, .nav__link:hover {\n  color: var(--skin-color);\n}\n\n/*===== Home Section =====*/\n.home {\n  background: url(\"../../images/home-bg.jpg\");\n  background-size: cover;\n  background-position: center center;\n  height: 100vh;\n}\n.home__container {\n  position: relative;\n  height: 100%;\n  align-items: center;\n}\n.home__social {\n  position: absolute;\n  top: 1.8rem;\n  left: 0;\n  display: flex;\n  align-items: center;\n  column-gap: 3.5rem;\n}\n.home__social-follow {\n  font-weight: var(--font-medium);\n  position: relative;\n}\n.home__social-follow::after {\n  content: \"\";\n  position: absolute;\n  width: 1rem;\n  height: 2px;\n  background-color: var(--text-color);\n  right: -45%;\n  top: 50%;\n}\n.home__social-links {\n  display: inline-flex;\n  column-gap: 1rem;\n}\n.home__social-link {\n  font-size: 1.08rem;\n  color: var(--text-color);\n  transition: 0.3s;\n}\n.home__social-link:hover {\n  transform: translateY(0.25rem);\n}\n.home__img {\n  display: none;\n}\n.home__title {\n  font-size: var(--biggest-font-size);\n}\n.home__subtitle {\n  font-size: var(--h3-font-size);\n  font-weight: var(--font-medium);\n  margin-bottom: var(--mb-0-75);\n}\n.home__description {\n  max-width: 450px;\n  margin-bottom: var(--mb-2);\n}\n\n.my__info {\n  display: flex;\n  column-gap: 1.8rem;\n  position: absolute;\n  left: 0;\n  bottom: 1.8rem;\n}\n\n.info__item {\n  display: flex;\n  align-items: center;\n}\n\n.info__title, .info__subtitle {\n  font-size: var(--small-font-size);\n}\n\n.info__title {\n  font-weight: var(--font-medium);\n}\n\n.info__icon {\n  font-size: 1.8rem;\n  color: var(--skin-color);\n  margin-right: var(--mb-0-75);\n}\n\n.about__container {\n  grid-template-columns: repeat(2, 1fr);\n  column-gap: 4rem;\n  align-items: center;\n}\n.about__img {\n  width: 480px;\n  border-radius: 0.75rem;\n  justify-self: center;\n}\n.about__heading {\n  font-size: var(--h3-font-size);\n  margin-bottom: var(--mb-0-75);\n}\n.about__description {\n  text-align: justify;\n  padding-right: 6rem;\n  margin-bottom: var(--mb-2);\n}\n.about__info {\n  display: grid;\n  grid-template-columns: repeat(3, 140px);\n  column-gap: 0.5rem;\n  margin-bottom: var(--mb-3);\n}\n.about__box {\n  text-align: center;\n  border-radius: 0.25rem;\n  padding: 1rem 1.25rem;\n  background-color: var(--box-color);\n}\n.about__icon {\n  font-size: 1.5rem;\n  color: var(--skin-color);\n  margin-bottom: var(--mb-0-75);\n}\n.about__title {\n  font-size: var(--small-font-size);\n}\n.about__subtitle {\n  font-size: var(--smaller-font-size);\n}\n\n/*===== Office Section =====*/\n.office__container {\n  grid-template-columns: repeat(3, 330px);\n  gap: 1.8rem;\n  justify-content: center;\n  padding-top: 1rem;\n}\n.office__filters {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  column-gap: 0.75rem;\n  margin-bottom: 2rem;\n}\n.office__item {\n  cursor: pointer;\n  color: var(--title-color);\n  padding: 0.25rem 0.75rem;\n  font-weight: var(--font-medium);\n  border-radius: 0.5rem;\n}\n.office__card {\n  background-color: var(--box-color);\n  padding: 1.25rem;\n  border-radius: 0.5rem;\n}\n.office__img {\n  border-radius: 0.5rem;\n  margin-bottom: var(--mb-1);\n}\n.office__title {\n  font-size: var(--normal-font-size);\n  font-weight: var(--font-medium);\n  margin-bottom: var(--mb-0-5);\n}\n.office__button {\n  color: var(--skin-color);\n  font-size: var(--small-font-size);\n  display: flex;\n  align-items: center;\n  column-gap: 0.25rem;\n  cursor: pointer;\n}\n.office__button-icon {\n  font-size: 1rem;\n  transition: 0.3s;\n}\n.office__button:hover .office__button-icon {\n  transform: translateX(0.25rem);\n}\n\n.contact__container {\n  grid-template-columns: 300px 340px;\n  column-gap: 3rem;\n  justify-content: center;\n  align-items: center;\n}\n.contact__info {\n  display: grid;\n  row-gap: 1rem;\n}\n.contact__card {\n  background-color: var(--box-color);\n  padding: 1rem;\n  border-radius: 0.5rem;\n  text-align: center;\n}\n.contact__card-icon {\n  font-size: 1.8rem;\n  color: var(--title-color);\n  margin-bottom: var(--mb-0-25);\n}\n.contact__card-title, .contact__card-data {\n  font-size: var(--small-font-size);\n}\n.contact__card-title {\n  font-weight: var(--font-medium);\n}\n.contact__card-data {\n  display: block;\n  margin-bottom: var(--mb-0-75);\n}\n.contact__button {\n  color: var(--skin-color);\n  font-size: var(--small-font-size);\n  display: flex;\n  align-items: center;\n  column-gap: 0.25rem;\n  cursor: pointer;\n  justify-content: center;\n}\n.contact__button-icon {\n  font-size: 1rem;\n  transition: 0.3s;\n}\n.contact__button:hover .contact__button-icon {\n  transform: translateX(0.25rem);\n}\n\n.input__container {\n  position: relative;\n  margin-top: 0.1rem;\n  margin-bottom: 1.9rem;\n}\n\n.input {\n  width: 100%;\n  border: 2px solid var(--text-color);\n  background-color: transparent;\n  padding: 0.6rem 1.2rem;\n  color: var(--title-color);\n  font-weight: var(--font-medium);\n  font-size: var(--normal-font-size);\n  letter-spacing: 0.5px;\n  border-radius: 0.5rem;\n  transition: 0.3s;\n  outline: none;\n}\n\ntextarea.input {\n  padding: 0.8rem 1.2rem;\n  min-height: 140px;\n  border-radius: 0.5rem;\n  resize: none;\n}\n\n.input__cont